.card{
  height: 620px;
  filter: drop-shadow(3px 3px 3px #575757d0);
  overflow: auto;
}

.card::-webkit-scrollbar {
  display: none;
}

.card-content {
  padding-right: 25px;
  align-items: center;
}


.Duration {
  color: grey;
}

.Description {
  text-align: justify;
  padding-bottom: 10px;
}
.Logo {
  width: 200px;
  height: auto;
  align-content: center;
}

.Heading {
  font-size: 75px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

.college {
  font-size: 20px;
  font-weight: bold;
}

