.University {
  font-size: 25px !important;
  font-weight: bold;
  font-family: inherit;
}

.Project-title {
  font-size: 25px !important;
  font-weight: bold;
  font-family: inherit;
}

.Company {
  font-size: 20px !important;
  font-weight: bold;
  font-family: inherit;
}

.Location {
  font-size: 20px !important;
  font-family: inherit;
}

.Project-link {
  width: 30px;
  height: auto;
}

.Technology {
  font-size: 15px;
}

.card-container {
  display: flex;
  flex-wrap: nowrap;
  height: 100% !important;
  overflow-x: auto;
  width: inherit;
  margin: 0 !important;
}

.card-container::-webkit-scrollbar {
  display: none;
}
.row {
  margin: auto;
  max-width: 5in;
  min-width: 5in;
  /* height: 6in; */
}

.row .card {
  border-style: solid;
  margin: 20px;
  text-align: center;
